
export default {
  initBus: function(){
    this.$bus.$on("set_current_tool", type => {
      console.log(type)
      this.currentTool = type;
    });
    this.$bus.$on("open_tool", (type,b) => {
      var _type = ""
      switch (type){
        case "component":
          _type = "isComponentOpen"
          break;
        case "pencil":
          // _type = "isPencilOpen"
          break;
        case "eraser":
          // _type = "isEraserOpen"
          break;
      }
      if(!_type) return false
        
      this[_type] = b
    })
  }
}


