export const TOOL_ERASER = 'eraser';

export default (context) => {
  let stroke = null;
  let points = [];
  function clearArcFun(x,y,r,cxt){    //(x,y)为要清除的圆的圆心，r为半径，cxt为context
    var stepClear=1;//别忘记这一步  
    clearArc(x,y,r);
    function clearArc(x,y,radius){
       var calcWidth=radius-stepClear;  
       var calcHeight=Math.sqrt(radius*radius-calcWidth*calcWidth);  
 
       var posX=x-calcWidth;  
       var posY=y-calcHeight;  
                       
       var widthX=2*calcWidth;  
       var heightY=2*calcHeight;  
                       
       if(stepClear<=radius){  
          cxt.clearRect(posX,posY,widthX,heightY);  
          stepClear+=1;  
          clearArc(x,y,radius);  
       }  
     }  
 }

  const onMouseDown = (x, y, size) => {
    stroke = {
      tool: TOOL_ERASER,
      size,
      points: [{ x, y }]
    };
    
    return [stroke];
  };

  const drawLine = (item, { x, y }) => {
    context.save();
    clearArcFun(x,y,item.size,context)
    context.restore();
  };

  const onMouseMove = (x, y) => {
    if (!stroke) return [];
    const newPoint = { x, y };
    const start = stroke.points.slice(-1)[0];
    drawLine(stroke, start, newPoint);
    stroke.points.push(newPoint);
    points.push(newPoint);

    return [stroke];
  };

  const onDebouncedMouseMove = () => {
    const debouncedPoints = points;
    points = [];
    return [stroke, debouncedPoints];
  };

  const onMouseUp = (x, y) => {
    if (!stroke) return;
    onMouseMove(x, y);
    points = [];
    const item = stroke;
    stroke = null;
    return [item];
  };

  const draw = (item, animate) => {
    let time = 0;
    let i = 0;
    const j = item.points.length;
    for (i, j; i < j; i++) {
      if (!item.points[i - 1]) continue;
      if (animate) {
        setTimeout(drawLine.bind(null, item, item.points[i - 1], item.points[i]), time);
        time += 10;
      } else {
        drawLine(item, item.points[i - 1], item.points[i]);
      }
    }
  };

  return {
    onMouseDown,
    onMouseMove,
    onDebouncedMouseMove,
    onMouseUp,
    draw,
  };
};



