<template>
  <div class="handle-wrapper">
    <div class="handle-point" v-show="!isOpen" @click="open">
      <img src="../assets/images/handle_point@2x.png" alt="">
    </div>
    <ul class="handle-panel" v-show="isOpen">
      <Btn name="重置" :onTouch="onReset"  type="reset"></Btn>
      <Btn name="撤销" :onTouch="onRevoke" type="revoke"></Btn>
      <Btn name="保存" :onTouch="onRevoke" type="save"></Btn>
      <Btn name="提交" :onTouch="onRevoke" type="submit"></Btn>
      <Btn name="画板" :onTouch="onRevoke" type="myDraw"></Btn>
      <Btn name="切换" :onTouch="onRevoke" type="switchModel"></Btn>
    </ul>
    <div class="masker" v-show="isOpen" @click="open"></div>
  </div>
</template>

<script>
import Btn from "./DrawHandleBtn"

function defaultFn(){
  console.log("defaultFn")
}

export default {
  data() {
    return {
      isOpen:false,
    };
  },  
  props:{
    onRevoke:{
      type: Function,
      default: defaultFn
    },
     onReset:{
      type: Function,
      default: defaultFn
    }
  },
  components:{
    Btn
  },
  methods: {
    open(){
      this.isOpen = !this.isOpen
    }
  },
  mounted() {
    console.log(this.onRevoke)
  },
};
</script>

<style lang="less" scope>
.masker{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  // opacity: 0;
  z-index: 99;
}
.handle-wrapper {
  position: fixed;
  left: 0;
  top: 40%;
  img {
    width: 0.72/2rem;
  }
  .handle-panel {
    position: absolute;
    left: 30px;
    top: 50%;
    transform: translateY(-50%);
    width: 234/2px;
    height: 404/2px;
    background-color: rgba(79, 79, 79, 0.2);
    border-radius: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 10px;
    z-index: 999;
  }

  .handle-icon {
    flex: 0 0 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .icon {
      width: 72/2px;
      height: 72/2px;
      font-size: 0;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .text {
      color: #ffffff;
      text-align: center;
      font-size: 12px;
      line-height: 1.6em
    }
  }
}
</style>



