<template>
  <swiper :options="swiperOption" :not-next-tick="notNextTick" ref="mySwiper">
    <!-- slides -->
    <swiper-slide class="img-page-wrapper" v-for="(item,index) in imgData.pageCount" :key="index">
      <div
        class="img-wrapper"
        @click="selectImg(img)"
        v-for="(img,imageIndex) in imgData.arr"
        :key="imageIndex"
        :index="index"
        v-if="Math.floor(imageIndex / imgData.pageSize) == index"
      >
        <img :src="img.imgSrc" v-if="img.imgSrc">
      </div>
    </swiper-slide>
    <!-- <div class="swiper-scrollbar" slot="scrollbar"></div> -->
    <div class="swiper-pagination" slot="pagination"></div>
  </swiper>
</template>

<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  data() {
    return {
      notNextTick: true,
      swiperOption: {
        autoHeight: true,
        debugger: true,
        // scrollbar: {
        //   el: ".swiper-scrollbar"
        // },
        pagination: {
          el: '.swiper-pagination',
        },
        observer: true
      },
      imgData: {
        pageSize: 6,
        pageCount: 3,
        arr: [
          { imgSrc: require("../assets/images/component/grass1@2x.png") },
          { imgSrc: require("../assets/images/component/grass2@2x.png") },
          { imgSrc: require("../assets/images/component/grass3@2x.png") },
          { imgSrc: require("../assets/images/component/leaf1@2x.png") },
          { imgSrc: require("../assets/images/component/leaf2@2x.png") },
          { imgSrc: require("../assets/images/component/leaf3@2x.png") },

          { imgSrc: require("../assets/images/component/house1@2x.png") },
          { imgSrc: require("../assets/images/component/house2@2x.png") },
          { imgSrc: require("../assets/images/component/house3@2x.png") },
          { imgSrc: require("../assets/images/component/window1@2x.png") },
          { imgSrc: require("../assets/images/component/window2@2x.png") },
          { imgSrc: require("../assets/images/component/tree@2x.png") },

          { imgSrc: require("../assets/images/component/boy@2x.png") },
          { imgSrc: require("../assets/images/component/girl@2x.png") },
          { imgSrc: "" },
          { imgSrc: "" }
        ]
      }
    };
  },
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  methods: {
    selectImg(img) {
  
    }
  },
  created() {
   
  },
  mounted() {
    setTimeout(() => {
      // this.$bus.$emit("set_current_tool", "");
    }, 100);

  }
};
</script>

<style lang="less" scoped>
.img-page-wrapper {
  display: flex;
  flex-wrap: wrap;
}
.img-wrapper {
  width: 33.3333%;
  height: 150/2px;
  text-align: center;
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  img {
    // width: 100%;
    height: 80%;
  }
}
</style>



