<template>
  <li class="ctr-icon" :class="{[iconName]:true,'ac':currentTool==iconName}"   @touchend="changeState" ref="">
    <slot></slot>
  </li>
</template>

<script>
export default {
  data() {
    return {
      isAcitve: false
    };
  },
  // props: ["iconName","TouchEvent"],
  props: {
    iconName:{
      type: String,
    },
    isOpen:{
      type: Boolean,
    },
    currentTool:{
      type:String,
      default:""
    },
    touchEvent:{
      type: Function,
      default:()=>{
        console.log("1231231")
      }
    }
  },
  methods: {
    changeState(){
      this.isAcitve = !this.isAcitve;
      this.$bus.$emit("set_current_tool", this.iconName);
      this.$bus.$emit("open_tool", this.iconName, !this.isOpen)
    }
  },
  created() {

  }
};
</script>

<style lang="less" scope>
.ctr-icon {
  position: relative;
  width: 42/2px;
  height: 42/2px;
  background-position: center center;
  background-size: 100%;
  background-repeat: no-repeat;
  &.component {
    background-image: url("../assets/images/component@2x.png");
  }
  &.component.ac {
    background-image: url("../assets/images/component_ac@2x.png");
    
  }
  &.ac:after{
    content: ".";
    font-size: 0;
    position: absolute;
    top: -30px;
    left: 0;;
    width: 0;
    height: 0;
    border: 10.5px solid;
    border-color: #fff transparent transparent;
    z-index: 9999;
  }

  &.pencil {
    background-image: url("../assets/images/pencil@2x.png");
  }
  &.pencil.ac {
    background-image: url("../assets/images/pencil_ac@2x.png");
  }
  &.eraser {
    background-image: url("../assets/images/eraser@2x.png");
  }
  &.eraser.ac {
    background-image: url("../assets/images/eraser_ac@2x.png");
  }
}

</style>



