export function getXy(e, diffY=44) {
  let type = e.type;
  let x = 0,
    y = 0;
  
    switch (type) {
      case "touchend":
        x = e.changedTouches[0].clientX
        y = e.changedTouches[0].clientY - diffY
        break;
      default:
        x = e.touches[0].clientX;
        y = e.touches[0].clientY - diffY;
        break;
    }
  return [x, y]
}



