<template>
  <div id="app">
    <Tabbar ></Tabbar>
    <div id="page-content">
      <router-view/>
    </div>
  </div>
</template>

<script>
import Tabbar from "./components/Tabbar";
export default {
  name: "App",
  components: {
    Tabbar
  }
};
</script>

<style lang="less">
@import url("./assets/less/common");
html,
body {
  height: 100%;
  overflow: hidden;
  -webkit-overflow-scrolling: unset;
}
#app {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
}
#page-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 9
  // padding-top: 44px;
}
.range-slider{
  width: 100% !important
}
.range-slider-inner{
  .range-slider-fill{
    background-color: #c8c6c6
  }
  .range-slider-rail{
    background-color: #e9e9e9
  }
  .range-slider-knob{
    border: 2px solid #e7e7e7;
    box-shadow: none
  }
}

.swiper-pagination-bullet-active {
    opacity: 1;
    background: #989898;
}
.swiper-pagination-fraction, .swiper-pagination-custom, .swiper-container-horizontal > .swiper-pagination-bullets {
 bottom: 0
}
</style>



