<template>
  <li class="handle-icon" @touchend="onTouch">
      <div class="icon">
        <img :src="curImg" alt="">
      </div>
      <div class="text">{{name}}</div>
    </li>
</template>

<script>
export default {
   data() {
     return {
       resetImg:require("../assets/images/reset@2x.png"),
       revokeImg:require("../assets/images/revoke@2x.png"),
       saveImg:require("../assets/images/save@2x.png"),
       submitImg:require("../assets/images/submit@2x.png"),
       myDrawImg:require("../assets/images/myDraw@2x.png"),
       switchModelImg:require("../assets/images/switch_model@2x.png"),
       curImg:""
     }
   },
   props:["name", "type", "onTouch"],
   created() {
    this.curImg = this[this.type + "Img"]

   },
}
</script>


