<template>
  <div class="color-select">
    <div class="color-wrapper" v-for="item in colorData" :key="item.color" @touchstart="changeColor(item)">
      <div class="color-circle" :class="{'select':item.color===colorSelect}" :style="'backgroundColor:'+ item.color"></div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      colorData: [
        { color: "rgb(255,255,255)" },
        { color: "rgb(0,0,0)" },
        { color: "rgb(237,52,21)" },
        { color: "rgb(247,183,77)" },
        { color: "rgb(2,174,12)" },
        { color: "rgb(25,118,247)" },
        { color: "rgb(144,87,244)" },
        { color: "rgb(243,37,182)" }
      ],
      colorSelect:"rgb(0,0,0)"
    };
  },
  props:{
    onColorChange: Function
  },
  methods: {
    changeColor(item){
      this.colorSelect = item.color;
      this.onColorChange(item.color)
    }
  },
  created() {
    
  },
};
</script>
<style lang="less" scope>
.color-select {
  display: flex;
  justify-content: space-around;
  .color-wrapper {
    width: 0.54/2rem;
    height: 0.54/2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    .color-circle{
      width: .44/2rem;
      height: .44/2rem;
      box-sizing: border-box;
      border: 2px solid #ffffff;
      box-shadow: 0px 0px 16px 4px rgba(161, 161, 161, 0.1);
      border-radius: 50%;
    }
    .color-circle.select{
      width: 100%;
      height: 100%;
    }
  }
}
</style>



