<template>
  <div class="masker" v-show="isShow" @touch="touchEvent"></div>
</template>

<script>
export default {
  data() {
    return {
      isShow: false
    };
  },
  props: {
    // isShow: {
    //   type: Boolean,
    //   default: false
    // },
    touchEvent: {
      type: Function,
      default: function() {
        this.isShow = !this.isShow;
      }
    }
  },
  mounted() {
    this.$bus.$on("show_masker", () => {
      this.isShow = true;
    });
    this.$bus.$on("hide_masker", () => {
      this.isShow = false;
    });
  }
};
</script>

<style lang="less" scoped>
.masker {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  opacity: 0;
  z-index: 99;
  background-color: rgba(0, 0, 0, .4)
}
</style>



